/* -------- custom checkbox css start ------------ */
.custom-checkbox {
  display: flex;
  align-items: center;
}
.custom-checkbox input {
  padding: 0;
  height: initial;
  width: initial;
  margin-bottom: 0;
  display: none;
  cursor: pointer;
}

.custom-checkbox label {
  position: relative;
  cursor: pointer;
  color: #000a19;
  font-family: Lato;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.custom-checkbox label:before {
  content:'';
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: transparent;
  border: 2px solid #c5c7c9;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 6px; 
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 8px;
}


.custom-checkbox input:checked + label:after {
  content: '';
  display: block;
  position: absolute;
  top: 7px;
  left: 6px;
  width: 3px;
  height: 7px;
  border: solid #ffff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.custom-checkbox.indeterminate input + label:after {
  content: '';
  content: '';
  display: block;
  position: absolute;
  top: 54%;
  left: 6px;
  width: 3px;
  height: 0;
  border: solid #ffff;
  border-width: 0 2px 2px 0;

}
.custom-checkbox.indeterminate input:checked + label:after {
  content: '';
  display: block;
  position: absolute;
  top: 7px;
  left: 6px;
  width: 3px;
  height: 7px;
  border: solid #ffff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);

}
.custom-checkbox.indeterminate label:before {
  content:'';
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: var(--primary);
  border: 2px solid var(--primary);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 6px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  margin-right: 8px;
} 
.custom-checkbox.small label:before {
  padding: 6px;
}
.custom-checkbox.small input:checked + label:after {
  top: 4px;
  left: 5px;
  width: 3px;
  height: 7px
}
.custom-checkbox input:checked + label::before {background-color: var(--primary);border-color: var(--primary);}
.custom-checkbox.c-primary-variant input:checked + label::before {background-color: var(--primary-variant);border-color: var(--primary-variant);}
