.custom-checkbox.c-secondary-variant input:checked + label::after {border: solid var(--black)}
.custom-checkbox.c-dark-bg input:checked + label::before {background-color: var(--dark-bg);border-color: var(--dark-bg);}
.custom-checkbox.c-dark-bg-variant input:checked + label::before {background-color: var(--dark-bg-variant);border-color: var(--dark-bg-variant);}
.custom-checkbox.c-black input:checked + label::before {background-color: var(--black);border-color: var(--black);}
.custom-checkbox.c-gray input:checked + label::before {background-color: var(--gray);border-color: var(--gray);}
.custom-checkbox.c-gray input:checked + label::before {background-color: var(--gray);border-color: var(--gray);}
.custom-checkbox.c-light-gray input:checked + label::before {background-color: var(--light-gray);border-color: var(--light-gray);}
.custom-checkbox.c-light-gray input:checked + label::after {border: solid var(--black)}
.custom-checkbox.c-red input:checked + label::before {background-color: var(--red);border-color: var(--red);}
.custom-checkbox.c-orange input:checked + label::before {background-color: var(--orange);border-color: var(--orange);}
.custom-checkbox.c-la-rioja input:checked + label::before {background-color: var(--La-Rioja);border-color: var(--La-Rioja);}
.custom-checkbox.c-la-rioja input:checked + label::after {border: solid var(--black)}
.custom-checkbox.c-green input:checked + label::before {background-color: var(--green);border-color: var(--green);}
.custom-checkbox.c-pelorous input:checked + label::before {background-color: var(--Pelorous);border-color: var(--Pelorous);}
.custom-checkbox.c-daisy-bush input:checked + label::before {background-color: var(--Daisy-Bush);border-color: var(--Daisy-Bush);}
.custom-checkbox.c-violet input:checked + label::before {background-color: var(--Violet);border-color: var(--Violet);}
.custom-checkbox.c-mulberry input:checked + label::before {background-color: var(--Mulberry);border-color: var(--Mulberry);}
.custom-checkbox.c-buttercup input:checked + label::before {background-color: var(--Buttercup);border-color: var(--Buttercup);}
