.stepper {
  width: 100%;
}

.stepper-indicator {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  overflow: hidden;
}

.stepper-inner {
  display: flex;
  text-align: center;
  cursor: pointer;
  align-items: center;
  position: relative;
}

.stepper-number {
  flex: 0 0 40px;
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 35px;
  border-radius: 50%;
  position: relative;
  text-align: center;
  z-index: 1;
  border: 2px solid var(--gray);
  background-color: var(--white);
  color: var(--gray);
}
.stepper-indicator .stepper-inner:after {
  width: 999em;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 40px;
  background-color: #E8E8E8;
  content: "";
}
.stepper-indicator .stepper-inner:first-child:after {
  left: 50%;
}
.stepper-indicator .stepper-inner:last-child:after {
  right: 50%;
  left: auto;
}
.stepper-number.active {
  border: 2px solid var(--primary);
  color: white;
  background-color:var(--primary);
}

