  color: #5e636b;
  font-family: Lato;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}
.stepper-steps {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 2em;
}

.stepper-actions {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.stepper-actions button {
  height: 42px;
  border-radius: 4px;
  background-color: #2196f3;
  border: 0;
  transition: all .2s linear;
  margin-left: 5px;
  min-width: 100px;
  text-align: center;
  color: white;
  cursor: pointer;
  outline: 0;
  padding: 10px;
}

.line {
  top: 100px;
  width: 75%;
  background-color: #d5d5d5;
  height: 2px;
  position: absolute;
  left: 12%;
}
.stepper-number.active.done {
  background: var(--primary) url(./images/right.svg) no-repeat;
  background-position: center center;
}
.stepper-number.active.done span {
  display: none;
}
.stepper-inner.lg-stepper .stepper-number {
  flex: 0 0 48px;
  width: 48px;
  height: 48px;
  line-height: 42px;
  font-size: 16px;
}
