.stepper-inner.line-stepper .stepper-label label {
   flex:0 0 100%;
}
.stepper-inner.text-stepper .stepper-number {
   border: none;
}
.stepper-inner.text-stepper .stepper-number.active {
   background: var(--white);
   color: var(--primary);
}
.stepper-inner.text-stepper .stepper-number.active.done {
  background: var(--primary) url(./images/right.svg) no-repeat;
  background-position: center center;
